	module.exports = {"data":{"markdownRemark":{"html":"<p>Hey there, we're IET.</p>\n<p>IET is a team of programmers in the Department of Innovation and Technology (DoIT) at the <a href=\"http://www.detroitmi.gov/\">City of Detroit</a>.</p>\n<p>We partner with City departments to build digital tools that solve information challenges. Our projects aim to simplify internal work flows and make data more accessible to residents.</p>\n<p>IET stands for Innovation &#x26; Emerging Technology for now, but we're on a new year's mission to swap out \"innovation\" with something less buzzy. Say hello and drop ideas at <a href=\"mailto:iet@detroitmi.gov\">iet@detroitmi.gov</a>.</p>\n<h3>How we started</h3>\n<p>Our team came together about a year ago to \"build cool things\" with the small catch that there wasn't much of a foundation to build from. So, our first few months became dedicated to modernizing our <a href=\"https://github.com/CityOfDetroit/etl\">ETL tools</a> (read as tools to <em>extract-transform-load</em> data) and spinning up a more robust data warehouse. </p>\n<p>We also built our first few apps, which notably included:</p>\n<ul>\n<li>\n<p>solid waste survey</p>\n<ul>\n<li>DPW field inspectors used to paper survey commercial sites where the City picks up trash, but we helped them transition to a mobile survey that preloads parcel info</li>\n</ul>\n</li>\n<li>\n<p><a href=\"https://github.com/CityOfDetroit/crime-viewer\">crime-viewer</a></p>\n<ul>\n<li>Crime Viewer was designed and tested with Detroit's neighborhood radio patrol members who monitor crime stats and create filtered reports about their communities</li>\n</ul>\n</li>\n<li>\n<p><a href=\"https://github.com/CityOfDetroit/demo-alerts\">demo-alerts</a></p>\n<ul>\n<li>Demo Alerts is an SMS tool to share if, when and where demolitions are planned to occur near an address you care about and lead safe health tips</li>\n</ul>\n</li>\n<li>\n<p><a href=\"https://github.com/CityOfDetroit/zoom-nineteen\">zoom-nineteen</a></p>\n<ul>\n<li>This map breaks a lot of design rules and came together too quickly, but keeps reminding us that departments are eager to layer <em>all</em> the data and visualize City programs block-by-block, or affectionately at <code>{ 'zoom': 19 }</code></li>\n</ul>\n</li>\n</ul>\n<h3>How we work today</h3>\n<p>We work in the open and in iterations; we approach projects by identifying a problem, testing new ideas and developing prototypes. We use open source libraries and share our work on <a href=\"https://github.com/CityOfDetroit\">Github</a>.</p>\n<p>We're currently excited to be building:</p>\n<ul>\n<li>\n<p><a href=\"https://github.com/CityOfDetroit/route-explorer\">route-explorer</a> </p>\n<ul>\n<li>We're working with DDOT to redesign how we provide bus schedules on detroitmi.gov (spoiler: no longer exclusively printable PDFs)</li>\n</ul>\n</li>\n<li>\n<p><a href=\"https://github.com/CityOfDetroit/detroit-js-sdk\">detroit-js-sdk</a></p>\n<ul>\n<li>We're building a support library that makes a lot City open data easily available with a single <code>npm install</code></li>\n</ul>\n</li>\n<li>\n<p><a href=\"https://github.com/CityOfDetroit/iet\">this website</a></p>\n<ul>\n<li>This is built with <a href=\"https://www.gatsbyjs.org/\">Gatsby.js</a>, a static site generator for React and GraphQL, and it's really just a very enjoyable developer experience</li>\n</ul>\n</li>\n</ul>\n<p>Contributions in the form of feature requests, bug reports and code patches are welcome to all our projects. Find notes on using Github issues and pull requests in our <a href=\"https://github.com/cityofDetroit/code-standards\">Code Standards</a> repository, or send us an <a href=\"mailto:iet@detroitmi.gov\">email</a>.</p>\n<h3>How we'll be using this blog</h3>\n<p>You can expect to read about:</p>\n<ul>\n<li><strong>Open data changelogs</strong>: monthly updates and revisions for <a href=\"https://data.detroitmi.gov\">https://data.detroitmi.gov</a> </li>\n<li><strong>Project launches</strong>: posts about tools or apps that we've built and how you can use them</li>\n<li><strong>Code snippets</strong>: technical tips, tricks, and cool things we're learning</li>\n<li><strong>Team updates</strong>: reflections and occasional job opportunities</li>\n</ul>\n<p>Thanks for reading, and more soon.</p>","frontmatter":{"title":"Hello, Internet","date":"January 08, 2018","tags":["hello-world","iet"]}}},"pathContext":{"slug":"/blog/hello-internet/"}}

